<template>
  <div>
    <router-view></router-view>
    <div class="content-blocker">Habis pada {{ get_masa_aktif }}</div>
  </div>
</template>

<style lang="scss">
// 3rd party plugins css
@import "~bootstrap-vue/dist/bootstrap-vue.css";
@import "~perfect-scrollbar/css/perfect-scrollbar.css";
@import "~socicon/css/socicon.css";
@import "~animate.css";
@import "~@fortawesome/fontawesome-free/css/all.css";
@import "~line-awesome/dist/line-awesome/css/line-awesome.css";
@import "assets/plugins/flaticon/flaticon.css";
@import "assets/plugins/flaticon2/flaticon.css";
@import "assets/plugins/keenthemes-icons/font/ki.css";

// Main demo style scss
@import "assets/sass/style.vue";

// Check documentation for RTL css
// Update HTML with RTL attribute at public/index.html
/*@import "assets/css/style.vue.rtl";*/
</style>

<script>
import { OVERRIDE_LAYOUT_CONFIG } from "@/core/services/store/config.module";
import CryptoService from "@/core/services/crypto.service";
import { mapGetters } from "vuex";

export default {
  name: "MarketWatch",
  mounted() {
    /**
     * this is to override the layout config using saved data from localStorage
     * remove this to use config only from static json (@/core/config/layout.config.json)
     */
    this.$store.dispatch(OVERRIDE_LAYOUT_CONFIG);
  },
  computed: {
    ...mapGetters(["currentUser", "isAuthenticated", "loginToken"]),
    get_masa_aktif() {
      let tzoffset = new Date().getTimezoneOffset() * 60 * 1000; //offset in milliseconds
      if (this.loginToken.length == 32) {
        return new Date(Date.now() - tzoffset).toISOString().split("T")[0];
      } else {
        try {
          let payload = JSON.parse(CryptoService.decrypt(this.loginToken));
          return new Date(payload.expired - tzoffset).toLocaleString("id-ID");
        } catch (e) {
          return "Indefinite";
        }
      }
    },
  },
};
</script>
